#loginModal {
  width: 50vw;
  height: 80vh;
  background: -webkit-gradient(linear, left top, left bottom, from(#3686A0), to(#b4bdc0));
  background: -webkit-linear-gradient(#3686A0, #b4bdc0);
  background: -o-linear-gradient(#3686A0, #b4bdc0);
  background: linear-gradient(#3686A0, #b4bdc0);
  border: none;
  border-radius: 5px;
  /*background-color: #3686A0;*/
  color: white;
  position: fixed;
  top: 0;
  left: 0;
  margin-left: 25vw;
  margin-top: 10vh;
  -webkit-transition: all 500ms cubic-bezier(1.000, 0.085, 0.640, 0.810); 
     -o-transition: all 500ms cubic-bezier(1.000, 0.085, 0.640, 0.810); 
        transition: all 500ms cubic-bezier(1.000, 0.085, 0.640, 0.810); /* custom */
  overflow: scroll;
  z-index: 100;
  -webkit-box-shadow: 0 0 50px -10px #000000;
  box-shadow: 0 0 50px -10px #000000;
}

#loginModal.hide {
  -webkit-transform: translate3d(0, -100vh, 0);
          transform: translate3d(0, -100vh, 0);
}

#loginModal.show {
  -webkit-transform: translate3d(0, 0vh, 0);
          transform: translate3d(0, 0vh, 0);
  overflow: scroll;
}

.headerText {
  width: 100%;
  text-align: center;
  display: inline-block;
}

#loginLink {
  display: inline-block;
  width: 50%;
}

#signUpLink {
  display: inline-block;
  width: 50%;
}

.tabColor {
  background-color: #75a9bb;
  padding: 6vh 0px;
  -webkit-transition: all 0.3s cubic-bezier(0.000, 0.000, 0.230, 1);
  -o-transition: all 0.3s cubic-bezier(0.000, 0.000, 0.230, 1);
  transition: all 0.3s cubic-bezier(0.000, 0.000, 0.230, 1);
  -webkit-transform-origin: 0% 100%;
      -ms-transform-origin: 0% 100%;
          transform-origin: 0% 100%;
}

.tabColor:hover {
  background-color: #67a0b4;
}

.focusedTab {
  -webkit-transition: all 0.3s cubic-bezier(0.000, 0.000, 0.230, 1);
  -o-transition: all 0.3s cubic-bezier(0.000, 0.000, 0.230, 1);
  transition: all 0.3s cubic-bezier(0.000, 0.000, 0.230, 1);
  -webkit-transform-origin: 0% 100%;
      -ms-transform-origin: 0% 100%;
          transform-origin: 0% 100%;
}

.focusedTab:hover {

}

#closeX {
  width: 3%;
  margin-right: 1.5vw;
  margin-top: 6vh;
  -webkit-transition: all .15s cubic-bezier(0.000, 0.000, 0.230, 1);
  -o-transition: all .15s cubic-bezier(0.000, 0.000, 0.230, 1);
  transition: all .15s cubic-bezier(0.000, 0.000, 0.230, 1);
}

#closeX:hover {
  cursor: pointer;
  -webkit-transform: scale(1.25); /* Safari */
  -ms-transform: scale(1.25);
      transform: scale(1.25);
}


.buttonPosition {
  width: 100%;
  text-align: center;
}

.headerText #signUpLink {
  color: white;
  text-decoration: none;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-image: -webkit-gradient( linear, left top, right top, from(white), color-stop(50%, white), color-stop(50%, white));
  background-image: -webkit-linear-gradient( left, white, white 50%, white 50%);
  background-image: -o-linear-gradient( left, white, white 50%, white 50%);
  background-image: linear-gradient( to right, white, white 50%, white 50%);
  background-size: 200% 100%;
  background-position: 100%;
}

.headerText #loginLink {
  color: white;
  text-decoration: none;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-image: -webkit-gradient( linear, left top, right top, from(white), color-stop(50%, white), color-stop(50%, white));
  background-image: -webkit-linear-gradient( left, white, white 50%, white 50%);
  background-image: -o-linear-gradient( left, white, white 50%, white 50%);
  background-image: linear-gradient( to right, white, white 50%, white 50%);
  background-size: 200% 100%;
  background-position: 100%;
}

#signUpLink:hover {
  cursor: pointer;
  -webkit-transition: all 1s cubic-bezier(0.000, 0.000, 0.230, 1);
  -o-transition: all 1s cubic-bezier(0.000, 0.000, 0.230, 1);
  transition: all 1s cubic-bezier(0.000, 0.000, 0.230, 1);
  background-position: 0%;
}

#loginLink:hover {
  cursor: pointer;
  -webkit-transition: all 1s cubic-bezier(0.000, 0.000, 0.230, 1);
  -o-transition: all 1s cubic-bezier(0.000, 0.000, 0.230, 1);
  transition: all 1s cubic-bezier(0.000, 0.000, 0.230, 1);
  background-position: 0%;
}


