.aboutContainer {
	width: 75vw;
	padding: 20px;
	margin-top: 2%;
	font-size: 20px;
	color: #2b2b2b;
	margin: auto;
	position: absolute;
	left: 12vw;
}

#aboutHeaderText {
  font-size: 40px;
  margin: 0px;
  color: #2b2b2b;
}

.aboutHeader {
	width: 100%;
	height: 10vh;
    margin-top: 0px;
    background-color: white;
    padding: 5vh 0 0 0;
    -webkit-box-shadow: 0 0 30px -10px #000;
	box-shadow: 0 0 30px -10px #000;
}

#aboutBackground {
	position: relative;
	z-index: -1;
	float: left;
	width: 100vw;
}

.aboutContainer a {
	color: #628cab;
}


